<script setup lang="ts">
import TheButton from "~/components/UI/TheButton.vue";
import {useRouter} from "vue-router";
import {useTaskList} from "~/stores/tasks";
import {useAuthStore} from "~/stores/auth";
import {useCompanies} from "~/stores/companies";
import {useTreeStore} from "~/stores/tree";
import NavigationLink from "~/components/Navigtaion/NavigationLink.vue";
import NavigationBlock from "~/components/Navigtaion/NavigationBlock.vue";

const loadCurrentUser = useAuthStore()
const taskList = useTaskList()
const company = useCompanies()
const router = useRouter()

const logOut = () => {
  localStorage.removeItem('token')
  router.push('/')
}

const isValidLink = (nameEn) => {
  const foundItem = company.company.modules?.find(item => item.name_en === nameEn);
  return foundItem !== undefined;
}

onMounted(() => {
  window.onscroll = (e) => {
    (loadCurrentUser.my_scroll) = window.scrollY;
  }
})


watch(() => loadCurrentUser.user, () => {
  if(loadCurrentUser.user.role.name_en !== 'admin'){
    company.loadCurrentCompany()
  }
})



</script>

<style scoped lang="scss">
* {
  ::-webkit-scrollbar {
    display: none;
  }
}
</style>